

























































































































































































import { Component, Vue, Prop } from 'vue-property-decorator'
import { getUsers, delUser, changeEnabled } from '@/api/users'
import Pagination from '@/components/Pagination/index.vue'
// import UserView from '@/views/system/user/userInfo.vue'

@Component({
  name: 'User',
  components: {
    Pagination
    // UserView
  }
})
export default class User extends Vue {
  private id = 0
  private total = 0
  private list = []
  private listLoading = true
  private listQuery = {
    current: 1,
    size: 20
  }
  private saveUserShow = false

  private dialogStatus = 'create'

  created () {
    this.getList()
  }

  private async getList () {
    this.listLoading = true
    const { data } = await getUsers(this.listQuery)
    this.list = data.records
    this.total = data.total
    // Just to simulate the time of the request
    setTimeout(() => {
      this.listLoading = false
    }, 0.5 * 1000)
  }

  private enabledChange (_id: string, value: boolean) {
    // changeEnabled(_id, value).then((res: any) => {
    //   this.$message({
    //     type: res.success ? 'success' : 'error',
    //     message: res.msg
    //   })
    // })
  }

  private handleCreate () {
    this.id = 0
    this.saveUserShow = true
    this.dialogStatus = 'create'
  }

  private handleEdit (_id: number) {
    this.id = _id
    this.dialogStatus = 'update'
    this.saveUserShow = true
  }

  private handleDelete (id: string) {
    this.$confirm('此操作将删除该用户, 是否继续?', '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    }).then(() => {
      // delUser(id).then((res: any) => {
      //   if (res.code === 200) {
      //     this.$message({
      //       type: 'success',
      //       message: res.msg
      //     })
      //     this.getList()
      //   } else {
      //     this.$message({
      //       type: 'error',
      //       message: res.msg
      //     })
      //   }
      // })
    })
      .catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
  }

  private closeDialog () {
    this.saveUserShow = false
  }
}
